import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const LS = {
  load(){
    return JSON.parse(localStorage.getItem('vue-todos') || '[]')
  },
  save(data){
    return localStorage.setItem('vue-todos',JSON.stringify(data))
  }
}

const filter = {
  all(todos){
    return todos
  },
  active(todos){
    return todos.filter((todo)=>{
      return !todo.complete
    })
  },
  complete(todos){
    return todos.filter((todo)=>{
      return todo.complete
    })
  },
}

export default new Vuex.Store({
  strict:true,
  state: {
    todos:[
      {content:'todos-Content',complete:false},
      {content:'todos-Content',complete:true},
      {content:'todos-Content',complete:false},
    ]
  },
  getters:{
    todoIndex(state){
      return filter[state.route.name](state.todos)
      .map(todo=> state.todos.indexOf(todo))
    }
  },
  mutations: {
    SET_TODOS(state,data){
      state.todos = data
      //儲存LocalStorage
      LS.save(state.todos)
    },
    ADD_TODO(state,data){
      state.todos.push(data)
      LS.save(state.todos)
    },
    UPDATE_TODO(state,{index,data}){
      // state.todos[index] = data
      //上述這樣做會發生問題 資料只會在下次改動時跑出來
      state.todos[index].content = data.content
      state.todos[index].complete = data.complete
      LS.save(state.todos)
    },
    REMOVE_TODO(state,index){
      state.todos.splice(index,1)
      LS.save(state.todos)
    }

  },
  actions: {
    INIT_TODOS({commit}){
      //讀取LocalStorage
      commit('SET_TODOS',LS.load())
    }
  },
})
